/*Font Family variables*/
/* START :: Custom variable */
html, body {
  overflow-x: hidden;
}

button, a, input[type=submit] {
  outline: none !important;
  box-shadow: none !important;
  transition: all 0.3s;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

/*Bootstrap buttons variables*/
/*Bootstrap Heading variable*/
a:hover {
  color: #fff;
}

.light-theme a:hover {
  color: #000;
}

/*Color Variables*/
/*  Light Theme Colors  */
.btn {
  position: relative;
  background-color: #B08615;
  border-color: #B08615;
  color: #fff;
  font-weight: 500;
  font-family: "Mark-Medium";
  text-transform: uppercase;
  padding: 9.2px 20px;
  font-size: 0.875rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.btn .btn-icon {
  margin-right: 5px;
  padding: 0;
}
.btn .btn-icon > * {
  margin: 0;
  padding: 0;
}
.btn img {
  /*margin-right: 5px;*/
  max-width: 20px;
}
.btn:hover {
  border-color: #B08615;
  background-color: transparent;
  color: #B08615;
}
.btn svg {
  margin-right: 5px;
  max-width: 20px;
  /*path {
  	fill : $white; 
  }*/
}
.btn:hover svg path {
  fill: #B08615;
}
.btn.btn-outline {
  color: #B08615;
  /*height: 48px;
  line-height: 44px;*/
  padding-top: 13px;
  padding-bottom: 13px;
  background-color: transparent;
}
.btn.btn-outline:hover {
  background-color: #B08615;
  border-color: #B08615;
  color: #fff;
}
.btn.btn-secondary {
  background-color: #333333;
  border-color: #333333;
}
.btn.btn-secondary:hover {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
}
.btn.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn.btn-white-outline {
  color: #fff;
  border-color: #fff;
  background-color: transparent;
}
.btn.btn-white-outline:hover {
  background-color: #fff;
  border-color: #fff;
  color: #333333;
}
.btn.btn-gray-outline {
  color: #fff;
  border-color: #4C4C4C;
  background-color: transparent;
}
.btn.btn-gray-outline:hover {
  background-color: #4C4C4C;
  border-color: #4C4C4C;
}
.btn.btn-gray-outline:hover svg path {
  fill: #fff;
}
.btn.w_250 {
  width: 250px;
}
.btn.btn-gray {
  background-color: #3F3F3F;
  border-color: #3F3F3F;
}
.btn.btn-normal {
  text-transform: initial;
}
.btn.btn-left {
  text-align: left;
}
.btn.btn-right {
  text-align: right;
}

.header .btn {
  text-transform: initial;
  /*height: 40px;
  line-height: 36px;*/
  padding-top: 8.5px;
  padding-bottom: 8.5px;
}
.header .btn-transparent {
  background-color: transparent;
  border: none;
}
.header .btn-transparent:hover {
  background-color: transparent;
  border: none;
  color: #fff;
}
.header .btn-transparent:hover svg path {
  fill: #fff;
}
.header .btn {
  padding: 8.5px 15px;
}
.header .header-btn .btn {
  padding: 8.5px 20px;
}
.header .header-btn .btn-white-outline {
  padding-left: 10px;
  padding-right: 10px;
}

/* Sidebar Theme Button */
.theme-btn {
  background-color: #3F3F3F;
  border-radius: 6px;
  margin-bottom: 15px;
  padding: 4px;
}
.theme-btn .t_btn {
  font-weight: 400;
  font-family: "Mark-Regular";
  text-transform: initial;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;
  border-radius: 6px;
}
.theme-btn .btn-light {
  background-color: #3F3F3F;
  border: 0;
  cursor: pointer;
}
.theme-btn .btn-dark {
  background-color: #222222;
  cursor: initial;
}

.light-theme .btn-dark {
  cursor: pointer;
  background-color: transparent;
}
.light-theme .btn-light {
  cursor: initial;
  background-color: #fff;
}
.light-theme .btn.svgImg svg path,
.light-theme .btn svg path {
  fill: #fff;
}
.light-theme .btn:hover svg path {
  fill: #B08615;
}
.light-theme .btn.btn-gray {
  background-color: #f2f2f2;
  color: #333333;
