/*----------- toggle css -----------*/
header .nav-toggle {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .nav-toggle .hamburger {
  width: 24px;
  height: 24px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}
header .nav-toggle .hamburger span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: #fff;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
header .nav-toggle .hamburger span:nth-child(1) {
  top: 4px;
}
header .nav-toggle .hamburger span:nth-child(2) {
  top: 10px;
}
header .nav-toggle .hamburger span:nth-child(3) {
  top: 10px;
}
header .nav-toggle .hamburger span:nth-child(4) {
  top: 17px;
}
header .nav-toggle .hamburger.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}
header .nav-toggle .hamburger.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
header .nav-toggle .hamburger.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
header .nav-toggle .hamburger.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}
